* {
    box-sizing: border-box;
    user-select: none;
    /*font-family: roboto;*/
}

body {
    background-color: aliceblue;
}

.game {
    padding: 0px;   
}

.battle {
    display: flex;
    position: relative;
}

.spell-book {
    position: absolute;
    top: 5px;
    left: 5px;
    width: 752px;
    height: 552px;
    background-color: rgb(234, 248, 252);
    z-index: 10;
}

.battle--field {
    min-width: 762px;
    width: 762px;
    height: 562px;
    background-color: rgb(244, 232, 96);
    padding: 5px;
    border: 1px rgb(49, 28, 0) solid;
}

.battle--field--square {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 28px;
    width: 28px;
    margin: 0px 0px -4px 0px;
    border: 1px gray solid;
    cursor: pointer;
    user-select: none;
}

.highlight {
    background-color: lightgreen !important;
}

.btMapCreature {
    width: 28px;
    height: 28px;
    position: absolute;
}

.battle--menu {
    width: 440px;
    background-color: rgb(212, 250, 255);
    padding: 5px;
    margin-left: 0px;
    border: 1px rgb(49, 28, 0) solid;
}

.battle--menu--buttons-block {
    height: 50px;
    margin-bottom: 5px;
}

.battle--menu--button {
    height: 48px;
    margin-right: 5px;
    cursor: pointer;
}

.battle--menu--btnDesc {
    display: inline-flex;
    align-items: center;
    font-size: 0.9em;
    width: 100px;
    height: 48px;
}

.battle--button:hover {
    background-color: rgb(209, 196, 131);
}

.battle--creature-info {
    display: flex;
    flex-direction: column;
    /*width: 100%;*/
}

.crinfo--table {
    background-color: rgb(80, 40, 5);
    color: rgb(80, 40, 5);
    margin-top: -2px;
    border: 1px rgb(80, 40, 5) solid;
}

.crinfo--table tr {
	text-align: center;  
    height: 25px;
}

.crinfo--table td {
    width: 60px;
    font-weight: bold;
    background-color: rgb(234, 215, 198); 
}

.crinfo--table--main {
    background-color: rgb(80, 40, 5);
    color: rgb(80, 40, 5);
    border: 1px rgb(80, 40, 5) solid;
}

.crinfo--table--main tr {
    text-align: left;
    height: 25px;
}

.crinfo--table--main td {
    font-weight: bold;
    background-color: rgb(234, 215, 198);
    padding-left: 5px;
}

.crinfo--table--main .main-td-1 {
    width: 90px;
}

.crinfo--table--main .main-td-2 {
    width: 120px;
}

.crinfo--table--main .main-td-3 {
    width: 60px;
}

.crinfo--table--main--imgTd {
    text-align: center;
    width: 90px; 
    background-color: rgb(180, 146, 108) !important;
    padding: 0px !important;
}

.crinfo--table--main--img {
    width: 64px;
    height: 64px;
}

.crinfo--table--main--logo {
    width: 20px;
    height: 20px;
}

.battle--queue {
    background-color: rgb(217, 233, 248);
    padding: 5px 3px 3px 5px;
    margin-top: 5px;
    border: 1px black solid;
}

.battle--queue--img {
    width: 48px;
    height: 48px;
    background-color: rgb(165, 207, 207);
    margin: 0 2px 2px 0;
    border: 2px rgb(165, 207, 207) solid;
    cursor: pointer;
}
.battle--queue--img:hover {
    background-color: rgb(108, 248, 113);
    border: 2px green solid;
}

.current-turn {
    float: right;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 48px;
    font-weight: bold;
    background-color: rgb(234, 215, 198);
    color: rgb(80, 40, 5);
    border: 2px rgb(80, 40, 5) solid;
}

.skill-bar {
    width: 410px;
    margin-top: 10px;
}

.skill-bar--item {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 50px;
    background: linear-gradient(rgb(217, 233, 248), rgb(188, 217, 245));
    border: 4px rgb(26, 134, 177) outset;
    cursor: pointer;
}

.skill-bar--tooltip {
    position: absolute;
    top: -180px;
    right: 50px;
    color: white;
    width: 280px;
    background-color: rgb(29, 35, 34);
    border: 30px solid;
    border-image: url("img/blueframe.png") 50 50 50 50 repeat;
    z-index: 10;
}

.tooltip--content {
    margin: -15px -5px -5px -5px;
}

.tooltip--header {
    position: relative;
    display: flex;
    align-items: center;
    height: 40px;
    margin-bottom: 5px;
}

.tooltip--header-name {
    font-size: 1.1em;
    font-weight: bold;
}

.tooltip--header-image {
    position: absolute;
    right: -10px;
    width: 50px;
    height: 50px;
    background: linear-gradient(rgb(217, 233, 248), rgb(188, 217, 245));
    border: 4px rgb(26, 134, 177) outset;
}

.tooltip--description {
    font-style: italic;
    margin-top: 10px;
}

.spell-effects {
    margin-top: 10px;
}

.spell-effects--item {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 36px;
    background: linear-gradient(rgb(217, 233, 248), rgb(188, 217, 245));
    border: 4px rgb(26, 134, 177) outset;
    margin-right: 5px;
    cursor: pointer;
}

.spell-effects--tooltip {
    position: absolute;
    bottom: 40px;
    left: -100px;
    color: white;
    width: 220px;
    background-color: rgb(29, 35, 34);
    border: 30px solid;
    border-image: url("img/blueframe.png") 50 50 50 50 repeat;
}

.buff-effect {
    display: block;
    color: lightgreen;
}

.debuff-effect {
    display: block;
    color: red;
}




/*КОНЕЦ БИТВЫ*/
.GG {
    position: fixed;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    background-color: rgb(136, 245, 136);
    color: black;
    width: 100px;
    height: 100px;
    border: 10px black solid;
    transform: translate(-50%, -50%);
}
